<template>
  <div id="watch" ref="watch" class=" black">
    <v-container fluid>
      <v-row>
        <v-alert prominent class="mx-auto" type="error" v-if="errored">
          <v-row align="center">
            <v-col class="grow">
              <div class="title">Error!</div>
              <div>
                 .   .
              </div>
            </v-col>
            <v-col class="shrink">
              <v-btn @click="actions"> </v-btn>
            </v-col>
          </v-row>
        </v-alert>
        <v-col v-else cols="11" class="mx-auto">
          <v-row>
            <v-col cols="12" sm="12" md="8" lg="8">
              <v-skeleton-loader
                type="card-avatar, article, actions"
                tile
                large
              >
                <div ref="hello">
                    <!-- <video-player :options="videoOptions"/> -->
                  <!-- <v-responsive v-if="m3u8">
                    <player-hls :options="options"/>
                  </v-responsive>

                   <v-responsive v-else>
                    <player-mp4 :options="options" />
                  </v-responsive> -->

                  <v-responsive >
                    <fluid-player  :option="options" />
                  </v-responsive>

                  <v-card flat tile  class="card" color="black" >
                    <v-card-title class="pl-0 pb-0">{{
                      video.title
                    }}</v-card-title>
                    <div
                      class="d-flex flex-wrap justify-space-between"
                      id="btns"
                    >
                      <v-card-subtitle
                        class="pl-0 pt-0 pb-0 subtitle-1"
                        style="line-height: 2.4em;"
                      >
                        {{ video.views }} .<v-icon>mdi-circle-small</v-icon
                        >{{video.CreatedAt}}
                      </v-card-subtitle>
                      <v-card-actions class="pt-0 pl-0">
                        <!-- @click="createFeeling('like')" -->
                        <v-btn text 
                          ><v-icon
                            :class="
                              `pr-2${
                                feeling !== 'like'
                                  ? ' grey--text text--darken-1'
                                  : ''
                              }`
                            "
                            >mdi-thumb-up</v-icon
                          >{{ video.likes }}</v-btn
                        >
                      <!-- @click="createFeeling('dislike')" -->
                        <v-btn text 
                          ><v-icon
                            :class="
                              `pr-2${
                                feeling !== 'dislike'
                                  ? ' grey--text text--darken-1'
                                  : ''
                              }`
                            "
                            >mdi-thumb-down</v-icon
                          >
                          {{ video.dislikes }}</v-btn
                        >
                        <!-- <v-btn
                          :href="video.url"
                          text
                          class="grey--text text--darken-1"
                          ><v-icon>mdi-download</v-icon> Download</v-btn
                        > -->

                      </v-card-actions>
                    </div>
                  </v-card>
                </div>
                <!-- <v-row class="justify-space-between">    -->
 <!--  -->
                      <!-- <v-img src="https:/" aspect-ratio="1.7"></v-img> -->
                <div v-for="poster in posters" :key="poster._id">
                  <v-card width="100%" 
                  :href="poster.href"
                  target="_blank"    
                  >
                    <!-- <v-responsive :aspect-ratio="16/9"> -->
                      <!-- <v-card-title class="text-h6">
                         {{poster.title}}
                      </v-card-title> -->
                      <v-img
                        :src="poster.pic_url"
                        max-height="600"
                        width="100%"
                        contain
                        class="grey darken-4"
                      ></v-img>
                    <!-- </v-responsive> -->

                  </v-card>
                </div>
                <!-- </v-row> -->
              </v-skeleton-loader>
            </v-col>
            <!--  -->
            <v-col cols="12" sm="12" md="4" lg="4">
              <hr class="grey--text" />
              <h4 class="mb-3 mt-3">  </h4>
              <div
                v-for="(video, i) in  videos"
                :key="i"
                class="mb-5"
              >
                <!-- <v-skeleton-loader
                  class="mx-auto"
                  type="list-item-avatar-three-line"
                  
                  tile
                  large
                > -->
                <!-- :to="`/watch/${video._id}`" -->
                  <v-card
                    class="card"
                    tile
                    flat
                    @click="nextVideo(video._id)"
                    color="black"
                  >
                    <v-row no-gutters>
                      <v-col class="mx-auto" cols="12" sm="12" md="5" lg="5">
                        <!-- <v-responsive max-height="100%"> -->
                        <v-img
                          class="align-center"
                          height="110"
                          :src="
                            video.thumbnailUrl
                          "
                        >
                        </v-img>
                        <!-- </v-responsive> -->
                      </v-col>
                      <v-col>
                        <div class="ml-2">
                          <v-card-title
                            class="pl-2 pt-0 subtitle-1 font-weight-bold"
                            style="line-height: 1"
                          >
                            {{ video.title }}
                          </v-card-title>

                          <v-card-subtitle
                            class="pl-2 pt-2 pb-0"
                            style="line-height: 1"
                          >
                            {{ video.userId.channelName }}<br />
                            {{ video.views }} .
                            <v-icon>mdi-circle-small</v-icon>
                            {{ dateFormatter(video.CreatedAt) }}
                          </v-card-subtitle>
                        </div>
                      </v-col>
                    </v-row>
                  </v-card>
                <!-- </v-skeleton-loader> -->
              </div>
              <!-- <v-col cols="12" sm="12" md="12" lg="12"> -->
              <!-- <infinite-loading :identifier="infiniteId" @infinite="getVideos"> -->
                <div slot="spinner">
                  <v-progress-circular
                    indeterminate
                    color="red"
                  ></v-progress-circular>
                </div>
                <div slot="no-results"></div>
                <span slot="no-more"></span>
                <div slot="error" slot-scope="{ trigger }">
                  <v-alert prominent type="error">
                    <v-row align="center">
                      <v-col class="grow">
                        <div class="title">Error!</div>
                        <div>
                 .   .
                        </div>
                      </v-col>
                      <v-col class="shrink">
                        <v-btn @click="trigger"> </v-btn>
                      </v-col>
                    </v-row>
                  </v-alert>
                </div>
              <!-- </infinite-loading> -->
              <!-- </v-col> -->
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
    <signin-modal
      :openModal="signinDialog"
      :details="details"
      @closeModal="signinDialog = false"
    />
  </div>
</template>

<script>
import moment from 'moment'
import { mapGetters } from 'vuex'
import VideoService from '@/services/VideoService'
import SubscriptionService from '@/services/SubscriptionService'
import FeelingService from '@/services/FeelingService'
import SigninModal from '@/components/SigninModal'
// import VideoPlayer from "@/components/VideoPlayer.vue";
// import PlayerMp4 from'@/components/PlayerMp4'
// import PlayerHls from'@/components/PlayerHls'
import FluidPlayer from '../components/FluidPlayer.vue'



export default {
  name:"watch",
  components: {
    // AddComment,
    // CommentList,
    SigninModal,
    // VideoPlayer,
    // PlayerMp4,
    // PlayerHls,
    FluidPlayer
    
   
  },
  data: () => ({
      options:{
          posterUrl:"",
          url:"",
          ads: ""
        },
      // m3u8:true,
      posters:[],
      videoOptions: {
              autoplay: false,
              controls: true,
              muted:false,
              sources: []
          },
      loading: false,
      loaded: false,
      errored: false,
      videoLoading: false,
      subscribed: false,
      subscribeLoading: false,
      showSubBtn: true,
      feeling: '',
      video: {},
      videos: [],
      page: 1,
      signinDialog: false,
      details: {}
  }),
  computed: {
    ...mapGetters(['currentUser', 'getUrl', 'isAuthenticated'])
  },
  methods: {
    nextVideo(videoId){
      this.$router.push("/watch/"+videoId);
      this.$router.go()
    },
    async getPosters(){
      const posters= await  VideoService.getPosters({typee:3})
      // console.log(posters)
      posters.data.data.forEach(element => {
        this.posters.push(element)
      });
      
    },
   async getVideo(id) {
      this.errored = false
      this.videoLoading = true
      this.video = {}
      try {
        const video = await VideoService.getById(id)
        const ads= await  VideoService.getPosters({typee:4})
        if (!video) return this.$router.push('/')
        this.video = video.data.data[0]
        // console.log(video,'m3u8....')
        // this.videoOptions.sources.push({
        //         src:video.data.data[0].url,
        //         type: "application/x-mpegURL"
        //       })
        let playUrl=video.data.data[0].url
        this.options.posterUrl =video.data.data[0].thumbnailUrl
        this.options.url=playUrl
        // console.log(ads)
        this.options.ads=ads.data.data[0].video_url
        // this.$set(this.options,'posterUrl',video.data.data[0].thumbnailUrl)
        // let index= playUrl.lastIndexOf(".")
        // //
        // let ext = playUrl.substr(index+1)
        // if(ext==='mp4'){
        //   this.m3u8=false
        //   console.log(ext,'ext-------------')
        // }
        // console.log(this.options,"-options-------")
      } catch (err) {
        this.errored = true
        console.log(err)
      } 

    },
    async getVideos($state) {
      this.errored = false
      if (!this.loaded) {
        this.loading = true
      }
      let classifys=[4,5,6,7,10]
      let temp= classifys[Math.floor(Math.random() * classifys.length)]
      const videos = await VideoService.getAll('public', { page: this.page*20 ,classify:temp})
        .catch((err) => {
          console.log(err)
          this.errored = true
        })
        .finally(() => (this.loading = false))

      if (typeof videos === 'undefined') return

      if (videos.data.data.length) {
        // this.page += 1

        this.videos.push(...videos.data.data)
        if ($state) {
          $state.loaded()
        }

        this.loaded = true
      } else {
        if ($state) {
          $state.complete()
        }
      }
    },
    async checkSubscription(id) {
      if (!this.isAuthenticated) return

      this.loading = true
      const sub = await SubscriptionService.checkSubscription({ channelId: id })
        .catch((err) => {
          console.log(err)
        })
        .finally(() => {
          this.loading = false
        })

      if (!sub) return

      if (!sub.data.data._id) this.subscribed = false
      else this.subscribed = true
    },
    async checkFeeling(id) {
      if (!this.isAuthenticated) return

      this.loading = true
      const feeling = await FeelingService.checkFeeling({ videoId: id })
        .catch((err) => {
          console.log(err)
        })
        .finally(() => {
          this.loading = false
        })

      if (!feeling) return

      if (feeling.data.data.feeling === 'like') this.feeling = 'like'
      else if (feeling.data.data.feeling === 'dislike') this.feeling = 'dislike'
    },
    async createFeeling(type) {
      if (!this.isAuthenticated) {
        this.signinDialog = true
        this.details = {
          title:
            type === 'like' ? 'Like this video?' : "Don't like this video?",
          text: 'Sign in to make your opinion count.'
        }
        return
      }
      switch (true) {
        case type === 'like' && this.feeling === '':
          this.feeling = 'like'
          this.video.likes++
          // console.log('new like')
          break
        case type === 'like' && this.feeling === type:
          this.feeling = ''
          this.video.likes--
          // console.log('remove like')
          break
        case type === 'like' && this.feeling === 'dislike':
          this.feeling = 'like'
          this.video.dislikes--
          this.video.likes++
          // console.log('change to like')
          break
        case type === 'dislike' && this.feeling === '':
          this.feeling = 'dislike'
          this.video.dislikes++
          // console.log('new dislike')
          break
        case type === 'dislike' && this.feeling === type:
          this.feeling = ''
          this.video.dislikes--
          // console.log('remove dislike')
          break
        case type === 'dislike' && this.feeling === 'like':
          this.feeling = 'dislike'
          this.video.likes--
          this.video.dislikes++
        // console.log('change to dislike')
      }

      const feeling = await FeelingService.createFeeling({
        videoId: this.video._id,
        type
      }).catch((err) => {
        console.log(err)
      })

      if (!feeling) return
    },
    async subscribe() {
      if (!this.isAuthenticated) {
        this.signinDialog = true
        this.details = {
          title: 'Want to subscribe to this channel?',
          text: 'Sign in to subscribe to this channel.'
        }
        return
      }
      this.subscribeLoading = true
      const sub = await SubscriptionService.createSubscription({
        channelId: this.video.userId._id
      })
        .catch((err) => console.log(err))
        .finally(() => {
          this.subscribeLoading = false
        })

      if (!sub) return

      if (!sub.data.data._id) {
        this.subscribed = false
        this.video.userId.subscribers--
      } else {
        this.subscribed = true
        this.video.userId.subscribers++
      }
    },
    async updateViews(id) {
      const views = await VideoService.updateViews(id).catch((err) => {
        console.log(err)
      })
      if (!views) return

      this.video.views++
    },
    play(e) {
      console.log(e)
    },
    actions() {
      this.getVideo()
    },
    show(event) {
      if (event.target.innerText === 'SHOW MORE') {
        this.truncate = false
        event.target.innerText = 'SHOW LESS'
      } else {
        this.truncate = true
        event.target.innerText = 'SHOW MORE'
      }
    },
    truncateText(string = '', num) {
      if (string.length <= num) {
        return string
      }
      return string.slice(0, num)
    },
    dateFormatter(date) {
      // console.log(date,'date---------------');
      // console.log(moment(date).format("YYYY-MM-DD"));
      return moment(date).format("YYYY-MM-DD");
    }
  },

  // watch:{
  //   videoOptions:{

  //       handler(val){
  //            if(val){
  //             console.log(val)
  //            }
  //        },
  //        deep:true //true 
  //   }
  // },
  mounted() {
    this.getVideo(this.$route.params.id)
    this.getVideos(this.$state)
    this.getPosters()
  },
  // beforeRouteUpdate(to, from, next) {
  //   this.page = 1
  //   ;(this.loading = false), (this.loaded = false), (this.videos = [])
  //   this.infiniteId += 1
  //   this.getVideo(to.params.id)
  //   next()
  // }
}
</script>

<style lang="scss">

  gg1 {
    height:300px;
  }

video {
  max-width: 100%;
}

.grey-color {
  color: #7f7f7f !important;
}

#btns {
  border-bottom: 1px solid #e0d8d8;
}

button.v-btn.remove-hover-bg {
  background-color: initial !important;
  &:hover {
    background-color: #f9f9f9;
  }
}
</style>
