<template>
    <div class="fluid-component">
        <video ref="myVideoPlayer" controls crossorigin>
            <source :src="videoUrl.url"  ref="source"
                    data-fluid-hd
                    type='application/x-mpegURL'/>
        </video>
    </div>
</template>

<script>
  import fluidPlayer from 'fluid-player';

  export default {
    name: 'FluidPlayer',
    props: {
      option:{
        type:Object,
        default:()=>{}
      }
    },
    data() {
      return {
        player: null,
        videoUrl:{url:this.option.url}
      }
    },
    mounted() {
      // this.getPlay(this.option)
    },
    methods:{
      getPlay(option){
          let options = {
                      layoutControls: {
                          primaryColor: "#28B8ED",
                          preload: true,
                          playbackRateEnabled: true,
                          persistentSettings: {
                              volume:  false, // Default true
                              quality: false, // Default true
                              speed:   false, // Default true
                              theatre: false // Default true
                          },
                          controlForwardBackward: {
                              show: true // Default: false
                          },
                          posterImage:option.posterUrl || ''
                     },
                      vastOptions: {
                          allowVPAID: true, // Default false.
                          adList: [
                              {
                                  roll: 'preRoll',
                                  vastTag: option.ads || '',
                                  fallbackVastTags:[''],
                                  adText: 'Advertising supports us directly',   
                              },
                              // {
                              //     roll: 'midRoll',
                              //     vastTag: '',
                              //     timer: 4
                              // },
                          ], 
                          vastTimeout: 1000,
                          showProgressbarMarkers: true,
                      vastAdvanced: {
                              vastLoadedCallback:       (function() { console.log("Here one event") }),
                              noVastVideoCallback:      (function() { console.log("Here another") }),
                              vastVideoSkippedCallback: (function() { console.log("Here one more") }),
                              vastVideoEndedCallback:   (function() { console.log("Here's the last") })
                          }
                          
                      }
                        
                    }
          console.log(options,'options******************')
          this.player = fluidPlayer(this.$refs.myVideoPlayer, options)
      }
    },
    computed:{
      // videoUrl:()=>{
      //   return  this.option.url
      // }
    },
    watch:{
      option:{
        handler(varNew){
          // this.player.destroy();
          console.log(this,varNew)
          this.$set(this.videoUrl,'url',varNew.url)
          this.$refs.source.src=varNew.url
          this.getPlay(varNew)
          console.log(this.videoUrl,'video url=======')
          console.log(this.$refs.source.src,"soruce is")
        },
        deep:true
      }
    },
    unmounted() {
      if (!this.player) {
        return;
      }

      this.player.destroy();
    }
  }
</script>

<style scoped>
    @import "~fluid-player/src/css/fluidplayer.css";

    div.fluid-component, div.fluid-component > video {
        width: 100%;
        height: 100%;
    }
</style>